import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';

@Component({
  selector: 'svg-card',
  templateUrl: './svg-cards.component.html',
  styles: [],
      animations: [
        trigger('flipState', [
          state('flipped', style({
            transform: 'rotateY(180deg)'
          })),
          state('face', style({
            transform: 'rotateY(0)'
          })),
          transition('flipped => face', animate('300ms ease-out')),
          transition('face => flipped', animate('300ms ease-out'))
        ])  
      ]
})
export class CardComponent implements OnInit, OnChanges {

  @Input() card: string = 'spade_1';
  @Input() fill: string;
  @Input() scale: number = 1;
  @Input() state: string = 'flipped';
  @Input() back: string = 'back';
  private tempcard: string;
  private tempcolor: string;
  private width: number;
  private height: number;

  constructor() { }

  ngOnInit() {
    this.height = this.scale * 244.640;
    this.width = this.scale * 169.075;
    this.tempcard = this.card;
    this.tempcolor = this.fill;
    this.flipCard();
  }
  ngOnChanges() {
  if (this.state == 'flipped' && this.fill == '') {
    this.state = 'face';
  }
}
  toggleState() {
    this.state = this.state === 'flipped' ? 'face' : 'flipped';
    this.flipCard();
  }
  flipCard() {
    if (this.state == 'face') {
      this.card = this.tempcard;
      this.fill = '';
    } else {
      this.card = this.back;
      this.fill = this.tempcolor;
    }
  }
}
