import { Component, OnInit, Input} from '@angular/core';
import { DeckService } from './svg-deck.service';

@Component({
  selector: 'svg-deck',
  templateUrl: './svg-deck.component.html',
  styleUrls: ['./svg-deck.component.css']
})
export class DeckComponent implements OnInit {
  
  //default scale = 1, color = red, state = face
  @Input() scale: number = .25;
  @Input() color: string = 'red';
  @Input() state: string = 'flipped';
  @Input() back: string = 'back';
  @Input() initsize: number = 100;
  //component deck array of cards
  private deck = [];

  constructor(private _deckService: DeckService) { }

  ngOnInit() {
    this.setScale(this.scale);
    this.setColor(this.color);
    this._deckService.scale.subscribe(data => this.scale = data,
      error => console.log(error));
    this._deckService.initsize.subscribe(data => this.initsize = data,
      error => console.log(error));
    this._deckService.color.subscribe(data => this.color = data,
      error => console.log(error));
    this._deckService.dealCard.subscribe(data => this.deck.push(data),
      error => console.log(error));
    this.resetCards();
    this.setSize(this.initsize);
  }
  setSize(number: number) {
    if (number > 1000) { return; }
    this.resetCards()
    setTimeout(() => { 
      this._deckService.setSize(number);
      for (let index = 0; index < this.initsize; index++) {
        this.getCard();
      }
     }, 1000);

  }
  toggleBack() {
    if (this.back == 'back') {
      this.back = 'alternate-back';
    } else {
      this.back = 'back';
    }
  }
  toggleFace() {
    if (this.state == 'face') {
      this.state = 'flipped';
    } else {
      this.state = 'face';
    }
    
  }
  getCard() {
    this._deckService.getCard();
  }
  setScale(value: number) {
    this._deckService.setScale(value);
  }
  setColor(color: string) {
    this._deckService.setColor(color);
  }
  resetCards() {
    this.deck = [];
  }
}